.card {
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.card__reviews {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__reviews-point {
  text-align: right;
  color: #060b35;
  align-items: center;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-label {
  color: #616070;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

h2, p {
  margin: 0;
}

